* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    width: 100%;
    height: 100vh;
    background: silver;
    font-family: 'Georama', sans-serif;
}

.app {
    width: 40%;
    margin: auto;
    min-width: 450px;
}

.app h3 {
    font-size: 102px;
    font-weight: 300;
    color: tomato;
    padding-bottom: 20px;
    text-align: center;
}

.app-contain {
    background: white;
    padding: 0 20px;
    border-radius: 5px;
}

.add {
    width: 100%;
    position: relative;
    padding: 10px 0;
}

.add input {
    width: 100%;
    padding: 10px 50px;
    outline: none;
    border: none;
    font-size: 18px;
    font-family: 'Georama', sans-serif;
}

::-webkit-input-placeholder { 
    font-size: 18px;
    font-family: 'Georama', sans-serif;
  }

.add i {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    padding-left: 15px;
    font-weight: 600;
}

.list {
    border-top: 1px solid silver;
    max-height: 350px;
    overflow-y: scroll;
}

::-webkit-scrollbar {
    width: 0px;
}

.list .item {
    display: flex;
    padding: 15px 10px;
    border-bottom: 1px solid silver;
}

.item p {
    flex-grow: 1;
    margin: auto;
    padding: 0 20px;
    font-size: 20px;
}

.item input[type='text'] {
    flex-grow: 1;
    margin: 0 20px;
    outline: none;
    padding: 14px 0;
    font-size: 20px;
}

.item input {
    margin: auto;
    display: block;
    width: 20px;
    height: 20px;
}

.item i {
    padding-right: 10px;
    margin: auto;
}

.item i:hover{
    color: tomato;
    cursor: pointer;
}

.filter-wrap {
    display: flex;
    justify-content: space-between;
    padding: 10px 0;
    border-top: 1px solid silver;
}

.filter-wrap .filter-content {
    display: flex;
    margin: auto 0;
}

.filter-wrap p {
    margin: auto 0;
}

.filter-content > p {
    padding: 5px 5px;
    margin: 0 5px;
}

.filter-content > p:hover,
.filter-wrap .clear-completed:hover {
    cursor: pointer;
    color: silver;
}
